* {
    box-sizing: border-box;
}

h5 {
    font-size: 30px;
    margin-top: -10px;
    margin-bottom: 30px;
}

.hero-Section {
    height: 700;
    padding-top: 0px;
    display: block;
    background: linear-gradient(#f8335e, transparent 90%),
        linear-gradient(0deg, #fff, transparent),
        #f8335e url('./flex_studentsbg.jpg') no-repeat center;
    background-image: url("./flex_studentsbg.jpg");
    background-size: cover;
    background-position: right top;
    background-repeat: no-repeat;
    position: relative;
    overflow: hidden;
}


.zagoo_main1 {
    float: left;
    margin-left: 120px;
    text-align: center;
    padding-top: 250px;
    width: 500px;
    color: white;
}

.zagoo_side {
    float: right;
    margin-left: 150px;
}

/* New Styles */

.hero-subscribe-from input {
    width: 400px;
    border: 1px solid #ebebeb;
    background: #fff;
    height: 48px;
    padding: 0 25px;
    border-radius: 5px;
    font-family: "Futura", sans-serif;
    font-size: 16px;
    margin-right: 8px;
    margin-bottom: 20px;
}


/*-----------------------------
Sign In Form
-------------------------------*/

.login-page1 {
    width: 460px;
    padding: 8% 0 0;
    margin-right: 120px;
    float: right;
    margin-top: -50px;
}

.form{
    z-index: 1;
    background: #FFFFFF;
    max-width: 410px;
    margin: 0 auto 100px;
    padding: 40px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.form_student input {
    font-family: "Roboto", sans-serif;
    outline: 0;
    background: #f2f2f2;
    width: 100%;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
    color: #3d3c3c
}

.form button {
    outline: 0;
    background: #3e2bce;
    width: 100%;
    border: none;
    padding: 15px;
    color: #FFFFFF;
    font-size: 18px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
}

.form button {
    /* FF3.6-15 */
    /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#3e2bce), color-stop(100%, #d32d3b), color-stop(100%, #2dd3aa), to(#2dd3aa));
    background: -o-linear-gradient(left, #3e2bce 0%, #d32d3b 100%, #d32d3b 100%, #d32d3b 100%);
    background: linear-gradient(to right, #3e2bce 0%, #d32d3b 100%, #d32d3b 100%, #d32d3b 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid: DXImageTransform.Microsoft.gradient(startColorstr='#3e2bce', endColorstr='#d32d3b', GradientType=1);
    /* IE6-9 */
}

.form button:hover,
.form button:active,
.form button:focus {
    background: #3e2bce;
}

.form .message {
    margin: 15px 0 0;
    color: #b3b3b3;
    font-size: 12px;
}

.form .message a {
    color: #3e2bce;
    text-decoration: none;
}

.form .register-form {
    display: none;
}

.site-btn {
    position: relative;
    display: inline-block;
    padding: 15px 30px;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    border-radius: 50px;
    font-family: "Futura", sans-serif;
    min-width: 170px;
    text-align: center;
    border: 2px solid #3e2bce;
    cursor: pointer;
    color: #fff;
}

.site-btn,
.sb-gradients {
    /* FF3.6-15 */
    /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#3e2bce), color-stop(100%, #d32d3b), color-stop(100%, #2dd3aa), to(#2dd3aa));
    background: -o-linear-gradient(left, #3e2bce 0%, #d32d3b 100%, #d32d3b 100%, #d32d3b 100%);
    background: linear-gradient(to right, #3e2bce 0%, #d32d3b 100%, #d32d3b 100%, #d32d3b 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid: DXImageTransform.Microsoft.gradient(startColorstr='#3e2bce', endColorstr='#d32d3b', GradientType=1);
}

.site-btn:hover {
    color: #fff;
}


/* Media Queries */
@media (max-width: 1220px) {

    .zagoo_main,
    .login-page {
        width: 90%;
        margin: 0;
        padding-top: 20px;
        float: none;
    }
}

@media (max-width: 1220px) {

    hr {
        width: 300px;
    }
}

@media (max-width: 760px) {

    .zagoo_main,
    .login-page {
        width: 100%;
        margin: 0;
        padding-top: 20px;
        float: none;
    }
}